body{
  margin: 0px 0px;
  padding: 10px 10px;
  display: flex;
  flex-direction: column;
  align-items:center;
  width: 100vw;
  background-color: rgb(119, 236, 154);
}
.bar{
  margin-left: 10vw;
  background: rgb(111, 233, 196);
  width: 50vw;
  font-weight: bold;
  font-size: 30px;
}
.bar2{
  background: rgb(111, 233, 196);
  width: 70vw;
  font-weight: bold;
  font-size: 30px;
}
#editor{
  margin-left: 10vw;
  height: 50vh;
  width: 50vw;
  border-color: rgb(111, 233, 196);
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
}

#preview{
  border-color: rgb(111, 233, 196);
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  width: 70vw;
  height: auto;
  background: white;
}